import React from 'react';
class Reply extends React.Component{
    render(){
        let {hash2,txt2,hash1 ,time2}=this.props.data
        
        return(
            <div className="reply-content">
                <div className="user">
                    <img src={hash2} className="user" alt=""/><span className="reply-F">回复</span>
                </div><div className="master"><img src={hash1}/></div><span className="reply-m"> :</span>
            <div className="content2">{txt2}</div> <div className="time2">{time2}</div>
            
            </div>
        )
    }
}
export default Reply;


