//表单组件
import React from 'react';
import PropTypes from 'prop-types'
import * as Hash_fc from './Hash'

class LiuForm extends React.Component{
    
   tijiao(event){
        event.preventDefault();
    }
    //留言事件
    add(event){
        
        if(event.type=="keyup"&&event.keyCode!=13){
            return false;
        }
        let zz=/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/g;
        let todos2=this.props.data
        let email=this.refs.email.value;
        let E_hash="https://www.gravatar.com/avatar/"+Hash_fc.md5(email)+"?s=40";
        let txt=this.refs.txt.value;
        let result=zz.test(email)
        if(email==""&&txt==""){
            this.refs.tishi1.style.display="inline";
            this.refs.tishi2.style.display="inline"
        }
        if(email==""){
            this.refs.tishi1.style.display="inline"
            return false
        }
         else {
            this.refs.tishi1.style.display="none"
        };
        if(txt=="") 
        {   
            this.refs.tishi2.style.display="inline"
            return false
        }
            else  {
                this.refs.tishi2.style.display="none"
            }
         if(!result){
            this.refs.tishi3.style.display="inline"
            return false

         } 
         else{
            this.refs.tishi3.style.display="none"
            
         }  
        
        let time=new Date().toLocaleString();
        this.props.addItem(txt,E_hash,time);
        this.refs.email.value=""
        this.refs.txt.value="";
        
    }
    render(){
        return(
             <form className="Lform" onSubmit={this.tijiao.bind(this)}>
                <div className="L-form">
                    <div className="e-mail">
                    <input ref="email" type="text" className="email"placeholder="E-mail"/>
                    <span className="tishi1" ref="tishi1">该内容不能为空</span>
                    <span className="tishi3" ref="tishi3">请填写正确邮箱</span>
                    </div>
                    <div className="content">
                    <textarea ref="txt"  type="text" className="content-input"  id="exampleInputName2" placeholder="添加新留言"></textarea>
                    <span className="tishi2" ref="tishi2">该内容不能为空</span>
                    </div>
                    <div className="Btn">
                <button type="button" className="submit" onClick={this.add.bind(this)}>留言</button>
                    </div>
                
                </div>
            </form>
        );
    }
}
export default LiuForm;


