//留言列表组件
import React from 'react';
import LiuItem from './LiuItem';
import Reply from './Reply';
class LiuList extends React.Component{
    constructor(props){
        super(props)
        this.todos=this.props.data;
        this.todos2=this.props.data2;
        this.addreply=this.props.addreply;
        this.i=this.props.data3;
        this.ids=this.props.data_id;
    }
      //添加回复数据
 /*addreply(value,h_value,hash_value2){
     this.todos2.push({
         txt2:value,
        hash2:h_value,
        hash1:hash_value2
    })
    todos2:this.state.todos2;
   }*/
    render(){
        let todos=this.props.data;
        
        let todoItems=todos.map(item=>{
            return <LiuItem  key1={item.id} data={item} addreply={this.addreply} data2={this.todos} key2={this.todos2.id} />
        });
        let todos2=this.props.data2;
        let hash1=this.props.data.hash;
       
       let todoreplys=todos2.map(item=>{
           return <Reply data={item}  />
       })
        
        return (
            <div className="list"> {todoItems}{todoreplys}</div>
          


        );
    }
}

export default LiuList;


