//根组件
import React from 'react';
import LiuList from './component/LiuList';
import LiuForm from './component/LiuForm';


class LiuApp extends React.Component{

    constructor(props){
    
        super(props);

//初始状态
        this.ids=0;
        this.state={
                todos2:[],
                todos:[]
               
        };
        
        this.addItem=this.addItem.bind(this)
        this.addreply=this.addreply.bind(this)
    }
  
//添加留言数据
    addItem(value,h_value,t_value){
       this.state.todos.push(
            {   hash:h_value,
                id:this.ids++,
                text:value,
                time:t_value,
                done:0
                
               
            }
        )

        this.setState({
            todos:this.state.todos
        });
    }
    //添加回复数据
    addreply(value,h_value,hash_value2){
        this.state.todos2.push({
            time2:new Date().toLocaleString(),
            txt2:value,
           hash2:h_value,
           hash1:hash_value2
       })
       this.setState({
           todos2:this.state.todos2
       });
      }

    render(){
        return (
            <div className="container">

                <br/>
                <br/>
                <br/>
                <br/>
                <div className="panel panel-default">
                    <div className="panel-headingbg-danger">                       
                    </div>
                    <div className="panel-body">
                             <LiuList  data={this.state.todos}  data2={this.state.todos2} data_id={this.state.ids} addreply={this.addreply}/>
                             <LiuForm addItem={this.addItem} data={this.state.todos2} data2={this.state.ids}/>
                    </div>
                </div> 
            </div>
          
        );
    }
}

export default LiuApp;


