//留言内容组件
import React from 'react';
import * as Hash_fc from './Hash';
import Reply from './Reply';

class LiuItem extends React.Component{
    constructor(props){
        super(props)
        this.key1=this.props.key1;
        this.key2=this.props.key2;
    }
    disply(){
        this.refs.aa.style.visibility="visible";
        this.refs.reply.style.visibility="hidden"
    }
   add(event){
     
    if(event.type=="keyup"&&event.keyCode!=13){
        return false;
    }
    let id=this.props.key1
    let hash2=this.props.data2[id].hash
    let email2=this.refs.email2.value;
    let E_hash="https://www.gravatar.com/avatar/"+Hash_fc.md5(email2)+"?s=30";//哈希值转换
    let content2=this.refs.content2.value;
    if(email2=="")return false;
    if(content2=="") return false;
    this.props.addreply(content2,E_hash,hash2);
    
    this.refs.email2.value=""
    this.refs.content2.value="";
    this.refs.aa.style.visibility="hidden";
    this.refs.reply.style.visibility="visible"
}

    render(){
      
        let {text,time,done,id,hash}=this.props.data;

        return (
            <div className="item">
                    <div className="-time">{time}</div>
                    <div className="touxiang"><img  className="touxiang" src={hash }/><span className="say">留言:</span></div>
                    <div className="-content">{text}</div> 
                 
                    <div className="c-reply">
                    <div className="contanier-r" ref="aa" > 
                    <div className="email2-c"><input ref="email2" type="text" className="email2"placeholder="E-mail"/></div>
                    <div className="content2-c"><input ref="content2" type="text" className="content2"placeholder="回复内容"/></div>
                    <button onClick={this.add.bind(this)}>发送</button>
                    </div>    
                    <button className="reply" onClick={this.disply.bind(this)} ref="reply" >回复</button>
                    
                    </div>  
                    
        </div>  


        );
    }
}

export default LiuItem;


